.team-card {
	width: 100%;
	height: 100%;

	&__img {
		margin-bottom: 16px;
		border-radius: 4px;
		width: 100%;
		height: auto;
		object-fit: cover;
		aspect-ratio: 1;
	}

	&__name {
		display: inline-block;
		font-weight: 700;

		&:not(:last-child) {
			margin-bottom: 8px;
		}
	}
}
