@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Circe Bold";
  src: url("../fonts/CirceBold.woff2") format("woff2"), url("../fonts/CirceBold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

:root {
  --color-white: #fff;
  --color-black: #000;
  --color-grey-10: #f1f1f1;
  --color-grey-20: #f2f2f2;
  --color-grey-50: #f5f5f5;
  --color-grey-60: #f6f6f6;
  --color-grey-80: #f8f8f8;
  --color-grey-100: #e2e2e2;
  --color-grey-150: #e0e0e0;
  --color-grey-200: #e3e3e3;
  --color-grey-250: #e8e8e8;
  --color-grey-270: #dcdcdc;
  --color-grey-300: #ddd;
  --color-grey-350: #d5d5d5;
  --color-grey-370: #d7d7d7;
  --color-grey-400: #cacaca;
  --color-grey-420: #c8c8c8;
  --color-grey-450: #c7c7c7;
  --color-grey-500: #cecece;
  --color-grey-550: #939393;
  --color-grey-600: #8a8a8a;
  --color-grey-700: #707070;
  --color-grey-750: #616161;
  --color-grey-950: #7c7c7c;
  --color-dark-600: #3a3a3a;
  --color-dark-800: #2b2b2b;
  --color-orange-100: #c89569;
  --color-orange-300: #ab8474;
  --color-red-100: #ffe8e4;
  --color-red-200: #f5c6be;
  --color-red-300: #ff9a89;
  --color-red-450: #fd6a52;
  --color-red-500: #ef5238;
  --color-red-800: #dd3e24;
  --color-red-850: #d8391f;
  --color-red-900: #ff0505;
  --color-brown-300: #70574d;
  --color-brown-600: #623b2a;
  --rgba-color-dark-800-94: rgba(43, 43, 43, 94%);
  --font-main: "Open Sans";
  --font-secondary: "Circe Bold";
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font: inherit;
}

img {
  max-width: 100%;
}

input,
button,
textarea,
select {
  color: inherit;
  font: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

address {
  font-style: normal;
}

button,
input,
textarea {
  display: inline-block;
  padding: 0;
  background-color: transparent;
  border: 0;
}

button {
  cursor: pointer;
  appearance: none;
}

input {
  margin: 0;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

legend {
  padding: 0;
}

picture {
  line-height: 0;
}

label {
  cursor: pointer;
}

/* ===============================
=            Choices            =
=============================== */

.choices {
  position: relative;
  overflow: hidden;
  margin-bottom: 24px;
  font-size: 16px;
}

.choices:focus {
  outline: none;
}

.choices:last-child {
  margin-bottom: 0;
}

.choices.is-open {
  overflow: visible;
}

.choices.is-disabled .choices__inner,
.choices.is-disabled .choices__input {
  background-color: #eaeaea;
  cursor: not-allowed;
  -webkit-user-select: none;
  user-select: none;
}

.choices.is-disabled .choices__item {
  cursor: not-allowed;
}

.choices [hidden] {
  display: none !important;
}

.choices[data-type*=select-one] {
  cursor: pointer;
}

.choices[data-type*=select-one] .choices__inner {
  padding-bottom: 7.5px;
}

.choices[data-type*=select-one] .choices__input {
  display: block;
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
  margin: 0;
}

.choices[data-type*=select-one] .choices__button {
  background-image: url("data:image/svg+xml;base64,");
  padding: 0;
  background-size: 8px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -10px;
  margin-right: 25px;
  height: 20px;
  width: 20px;
  border-radius: 10em;
  opacity: 0.25;
}

.choices[data-type*=select-one] .choices__button:hover,
.choices[data-type*=select-one] .choices__button:focus {
  opacity: 1;
}

.choices[data-type*=select-one] .choices__button:focus {
  box-shadow: 0 0 0 2px #005F75;
}

.choices[data-type*=select-one] .choices__item[data-placeholder] .choices__button {
  display: none;
}

.choices[data-type*=select-one]::after {
  content: "";
  height: 0;
  width: 0;
  border-style: solid;
  border-color: #333 transparent transparent transparent;
  border-width: 5px;
  position: absolute;
  right: 11.5px;
  top: 50%;
  margin-top: -2.5px;
  pointer-events: none;
}

.choices[data-type*=select-one].is-open::after {
  border-color: transparent transparent #333;
  margin-top: -7.5px;
}

.choices[data-type*=select-one][dir=rtl]::after {
  left: 11.5px;
  right: auto;
}

.choices[data-type*=select-one][dir=rtl] .choices__button {
  right: auto;
  left: 0;
  margin-left: 25px;
  margin-right: 0;
}

.choices[data-type*=select-multiple] .choices__inner,
.choices[data-type*=text] .choices__inner {
  cursor: text;
}

.choices[data-type*=select-multiple] .choices__button,
.choices[data-type*=text] .choices__button {
  position: relative;
  display: inline-block;
  margin-top: 0;
  margin-right: -4px;
  margin-bottom: 0;
  margin-left: 8px;
  padding-left: 16px;
  border-left: 1px solid #003642;
  background-image: url("data:image/svg+xml;base64,");
  background-size: 8px;
  width: 8px;
  line-height: 1;
  opacity: 0.75;
  border-radius: 0;
}

.choices[data-type*=select-multiple] .choices__button:hover,
.choices[data-type*=select-multiple] .choices__button:focus,
.choices[data-type*=text] .choices__button:hover,
.choices[data-type*=text] .choices__button:focus {
  opacity: 1;
}

.choices__inner {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  background-color: #f9f9f9;
  padding: 7.5px 7.5px 3.75px;
  border: 1px solid #ddd;
  border-radius: 2.5px;
  font-size: 14px;
  min-height: 44px;
  overflow: hidden;
}

.is-focused .choices__inner,
.is-open .choices__inner {
  border-color: #b7b7b7;
}

.is-open .choices__inner {
  border-radius: 2.5px 2.5px 0 0;
}

.is-flipped.is-open .choices__inner {
  border-radius: 0 0 2.5px 2.5px;
}

.choices__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.choices__list--single {
  display: inline-block;
  padding: 4px 16px 4px 4px;
  width: 100%;
}

[dir=rtl] .choices__list--single {
  padding-right: 4px;
  padding-left: 16px;
}

.choices__list--single .choices__item {
  width: 100%;
}

.choices__list--multiple {
  display: inline;
}

.choices__list--multiple .choices__item {
  display: inline-block;
  vertical-align: middle;
  border-radius: 20px;
  padding: 4px 10px;
  font-size: 12px;
  font-weight: 500;
  margin-right: 3.75px;
  margin-bottom: 3.75px;
  background-color: #005F75;
  border: 1px solid #004a5c;
  color: #fff;
  word-break: break-all;
  box-sizing: border-box;
}

.choices__list--multiple .choices__item[data-deletable] {
  padding-right: 5px;
}

[dir=rtl] .choices__list--multiple .choices__item {
  margin-right: 0;
  margin-left: 3.75px;
}

.choices__list--multiple .choices__item.is-highlighted {
  background-color: #004a5c;
  border: 1px solid #003642;
}

.is-disabled .choices__list--multiple .choices__item {
  background-color: #aaaaaa;
  border: 1px solid #919191;
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  display: none;
  z-index: 1;
  position: absolute;
  width: 100%;
  background-color: #fff;
  border: 1px solid #ddd;
  top: 100%;
  margin-top: -1px;
  border-bottom-left-radius: 2.5px;
  border-bottom-right-radius: 2.5px;
  overflow: hidden;
  word-break: break-all;
}

.is-active.choices__list--dropdown,
.is-active.choices__list[aria-expanded] {
  display: block;
}

.is-open .choices__list--dropdown,
.is-open .choices__list[aria-expanded] {
  border-color: #b7b7b7;
}

.is-flipped .choices__list--dropdown,
.is-flipped .choices__list[aria-expanded] {
  top: auto;
  bottom: 100%;
  margin-top: 0;
  margin-bottom: -1px;
  border-radius: 0.25rem 0.25rem 0 0;
}

.choices__list--dropdown .choices__list,
.choices__list[aria-expanded] .choices__list {
  position: relative;
  max-height: 300px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
}

.choices__list--dropdown .choices__item,
.choices__list[aria-expanded] .choices__item {
  position: relative;
  padding: 10px;
  font-size: 14px;
}

[dir=rtl] .choices__list--dropdown .choices__item,
[dir=rtl] .choices__list[aria-expanded] .choices__item {
  text-align: right;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background-color: #f2f2f2;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted::after,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted::after {
  opacity: 0.5;
}

.choices__item {
  cursor: default;
}

.choices__item--selectable {
  cursor: pointer;
}

.choices__item--disabled {
  cursor: not-allowed;
  -webkit-user-select: none;
  user-select: none;
  opacity: 0.5;
}

.choices__heading {
  font-weight: 600;
  font-size: 12px;
  padding: 10px;
  border-bottom: 1px solid #f7f7f7;
  color: gray;
}

.choices__button {
  text-indent: -9999px;
  appearance: none;
  border: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.choices__button:focus {
  outline: none;
}

.choices__input {
  display: inline-block;
  vertical-align: baseline;
  background-color: #f9f9f9;
  font-size: 14px;
  margin-bottom: 5px;
  border: 0;
  border-radius: 0;
  max-width: 100%;
  padding: 4px 0 4px 2px;
}

.choices__input:focus {
  outline: 0;
}

.choices__input::-webkit-search-decoration,
.choices__input::-webkit-search-cancel-button,
.choices__input::-webkit-search-results-button,
.choices__input::-webkit-search-results-decoration {
  display: none;
}

.choices__input::-ms-clear,
.choices__input::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

[dir=rtl] .choices__input {
  padding-right: 2px;
  padding-left: 0;
}

.choices__placeholder {
  opacity: 0.5;
}

/* =====  End of Choices  ====== */

.header .choices {
  height: 100%;
}

.header .choices[data-type*=select-one]::after {
  right: 10px;
  width: 20px;
  height: 20px;
  filter: none;
}

.header .choices__inner {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  padding: 10px 20px 0;
  height: 100%;
  color: var(--color-white);
  background-color: var(--color-brown-600);
}

.header .choices__item {
  font-weight: 600;
  font-size: 13px;
  color: var(--color-grey-420);
}

.header .choices__item::before {
  display: none;
}

.header .choices.is-open .choices__inner {
  border-color: transparent;
  border-radius: 0;
  border-top-left-radius: 4px;
}

.header .choices.is-focused .choices__inner {
  border-color: transparent;
}

.header .choices__list {
  color: var(--color-white);
  background-color: var(--color-brown-600);
}

.header .choices__list--dropdown,
.header .choices__list[aria-expanded] {
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  padding: 12px 16px 0;
  background-color: var(--color-brown-600);
}

.header .choices__list--dropdown .choices__item,
.header .choices__list[aria-expanded] .choices__item {
  padding-bottom: 10px;
  font-size: 13px;
}

.header .choices__list--dropdown .choices__item:not(:first-child),
.header .choices__list[aria-expanded] .choices__item:not(:first-child) {
  border-top: 1px solid var(--color-brown-300);
  padding-top: 10px;
}

.header .choices__list--dropdown .choices__item--selectable.is-highlighted,
.header .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  color: var(--color-white);
  background-color: var(--color-brown-600);
}

.header .choices__list--single {
  padding-right: 20px;
}

.header .choices__list--single .choices__item {
  color: var(--color-white);
}

.choices[data-type*=select-one]::after {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  right: 4px;
  margin-top: 0;
  border: 0;
  width: 24px;
  height: 24px;
  background-image: url("../img/icons/arrow.svg");
  filter: invert(46%) sepia(49%) saturate(5737%) hue-rotate(337deg) brightness(94%) contrast(99%);
  transform: rotate(-180deg) translateY(50%);
  transition: transform 0.3s;
}

.choices[data-type*=select-one].is-open::after {
  margin-top: 0;
  transform: rotate(0) translateY(-50%);
}

.choices__inner {
  display: flex;
  align-items: center;
  border: 0;
  border-radius: 0;
  padding: 10px 32px 10px 4px;
  color: var(--color-dark-600);
  background-color: transparent;
}

.choices__item {
  position: relative;
  font-size: 14px;
  transition: color 0.3s;
}

.choices__item::before {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  right: 4px;
  top: 0;
  width: 16px;
  height: 16px;
  background-image: url("../img/icons/check.svg");
  opacity: 0;
  filter: invert(60%) sepia(61%) saturate(2714%) hue-rotate(341deg) brightness(98%) contrast(92%);
  transition: opacity 0.3s;
}

.choices__item.is-highlighted {
  padding-right: 30px;
}

.choices__item.is-highlighted::before {
  opacity: 1;
}

.choices.is-open .choices__inner,
.choices.is-focused .choices__inner {
  border-color: transparent;
}

.choices__placeholder {
  color: var(--color-dark-600);
  opacity: 1;
}

.choices__list {
  color: var(--color-dark-600);
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  z-index: 4;
  margin-top: 0;
  border: 0;
  border-color: transparent;
  padding: 22px 4px 16px;
  background-color: var(--color-white);
}

.choices__list--dropdown .choices__item,
.choices__list[aria-expanded] .choices__item {
  padding: 0;
  padding-bottom: 2px;
  font-size: 14px;
  transition: color 0.3s;
}

.choices__list--dropdown .choices__item:not(:last-child),
.choices__list[aria-expanded] .choices__item:not(:last-child) {
  padding-bottom: 16px;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  color: var(--color-red-500);
  background-color: transparent;
}

.choices__list--dropdown .choices__item--selectable[data-select-text],
.choices__list[aria-expanded] .choices__item--selectable[data-select-text] {
  padding-right: 30px;
}

.choices__list--single {
  padding: 0;
  padding-right: 20px;
}

.graph-modal {
  --transition-time: 0.3s;
  position: fixed;
  inset: 0;
  z-index: 1000;
  overflow: hidden auto;
  text-align: center;
  background-color: var(--rgba-color-dark-800-94);
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
  cursor: pointer;
}

.graph-modal::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.graph-modal__container {
  border-radius: 4px;
  padding: 24px;
  background-color: var(--color-white);
  position: relative;
  display: none;
  vertical-align: middle;
  margin: 50px auto;
  width: 100%;
  max-width: 576px;
  text-align: left;
  cursor: default;
}

.graph-modal__container--success .graph-modal__box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 20px;
  min-height: 200px;
}

.graph-modal__close {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  right: 24px;
  top: 24px;
  padding: 0;
  width: 24px;
  height: 24px;
  background-image: url("../img/icons/close.svg");
  filter: invert(74%) sepia(83%) saturate(1%) hue-rotate(314deg) brightness(95%) contrast(86%);
}

.graph-modal__header {
  margin-bottom: 32px;
}

.graph-modal__title {
  margin-bottom: 16px;
  font-family: var(--font-secondary);
  font-weight: 700;
  font-size: 24px;
  display: inline-block;
  padding-right: 46px;
}

.graph-modal__title sup {
  font-size: 14px;
  color: var(--color-red-500);
}

.graph-modal__text {
  line-height: 1.2;
}

.graph-modal__text p:not(:last-child) {
  margin-bottom: 8px;
}

.graph-modal__items {
  display: flex;
  flex-direction: row;
  gap: 24px 16px;
  flex-wrap: wrap;
}

.graph-modal__items:not(:last-child) {
  margin-bottom: 32px;
}

.graph-modal__item {
  flex-basis: calc(50% - 8px);
}

.graph-modal__item--long {
  flex-grow: 1;
  flex-basis: 100%;
}

.graph-modal__item--long textarea {
  height: 94px;
}

.graph-modal__info {
  width: 100%;
}

.graph-modal__subtext {
  font-size: 12px;
}

.graph-modal__subtext p:not(:last-child) {
  margin-bottom: 4px;
}

.graph-modal__subtext:not(:last-child) {
  margin-bottom: 8px;
}

.graph-modal .form__links {
  padding-left: 0;
}

.graph-modal .form__footer:not(:last-child) {
  margin-bottom: 32px;
}

.graph-modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal__container.graph-modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
}

.fade,
.fade.animate-open {
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
}

.fadeInUp,
.fadeInUp.animate-open {
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
}

