  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;
  display: inline-block;
  width: 24px;
  height: 24px;
}

.rating__star::before,
.rating__star::after {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../img/icons/star.svg");
  transition: opacity 0.3s;
}

.rating__star::after {
  filter: invert(95%) sepia(0%) saturate(0%) hue-rotate(243deg) brightness(97%) contrast(85%);
}

.rating__star::before {
  opacity: 0;
  filter: invert(82%) sepia(96%) saturate(495%) hue-rotate(302deg) brightness(82%) contrast(90%);
}

.rating__star:hover ~ .rating__hidden:checked ~ .rating__star::before {
  opacity: 1;
}

.rating__star:hover ~ .rating__hidden:checked ~ .rating__star::after {
  opacity: 0;
}

